<script>
    export default {
        name: 'DamageModal',
        props: [
            'other',
            'baseHP'
        ],
        data() {
            return {
                damage: 0,
                result: 0,
                armor: 0,
                active: false,
                state: "input",
                resultTexts: {
                    0: ["Czemu jeszcze stoisz?", "Najwidoczniej zapomniae jak si umiera."],
                    10: ["Zaraz padniesz.", "Uwaaj teraz eby si nie przeci papierem."],
                    20: ["Jest bardzo le.", "A wanie! Masz spisany testament?"],
                    30: ["Jest le.", "Nie wytrzymasz ju dugo. Zetrzyj t krew z oczu bo patrzysz w z stron."],
                    40: ["Cholera! To ma tak bole?.", "Nie wygldasz za dobrze. Jeli kiedykolwiek wygldae."],
                    50: ["Trzymasz si.", "Cho zdecydowanie wolaby teraz ten poranek na ktry tak narzekae."],
                    60: ["Nie jest tak le.", "Cho z pewnoci bywao lepiej."],
                    70: ["Zraniony przyzwoicie.", "Przygoda! Niczego innego nie oczekujesz od ycia."],
                    80: ["Nazywasz to zranieniem?", "Synu, cho, poka ci co to znaczy by zranionym."],
                    90: ["To tylko zadrapanie!", "\"W gorszym stanie byem wczorajszej nocy jak uderzyem palcem o st!\""],
                    100: ["Jeste w peni zdrowia!", " Cokolwiek to byo, trzeba troch wicej na ciebie."],
                }
            }
        },
        computed: {
            resultText() {
                return this.resultArrayText[0];
            },

            resultSubText() {
                return this.resultArrayText[1];
            },

            resultArrayText () {
                if (this.percentage == 0)
                    return ["Tracisz przytomno.", "Za par tur umrzesz. Dzikujemy za gr."];

                // For each result type, check if it's between
                // current and next one
                for(let rtId in this.resultTexts) {
                    rtId = parseInt(rtId);
                    let nextRtId = parseInt(rtId) + 10;

                    if ((this.percentage * 100) < nextRtId) {
                        return this.resultTexts[rtId];
                    }
                }

                // Defaults to > 100%
                return this.resultTexts[100];
            },

            currentHP () {
                return this.other.currentHP;
            },

            percentage() {
                return (this.currentHP / this.baseHP);
            },
        },
        methods: {
            // Reset data and show the modal
            show() {
                this.result = 0;
                this.damage = 0;
                this.state = "input";
                this.active = true;
            },

            async harm() {
                this.result = parseInt(this.damage) - parseInt(this.armor);

                this.other.currentHP = parseInt(this.currentHP) - parseInt(this.result);

                // HP can't come out negative
                if (this.other.currentHP < 0)
                    this.other.currentHP = 0;

                this.state = "result";
            },

            // Emits interface to parent
            emitInterface() {
                this.$emit("interface", {
                    showDamageModal: () => this.show()
                });
            }
        },
        watch: {
            active: function (val) {
                document.querySelector("html").classList.toggle("is-clipped", val);
            },
            // Keep damage not negative
            damage: function (val) {
                if (val < 0)
                    this.damage = 0;
            },
            // Keep armor not negative
            armor: function (val) {
                if (val < 0)
                    this.armor = 0;
            },
            // Hide modal on router change to prevent scroll lock
            $route () {
                this.active = false;
            }
        },
        mounted() {
            // Emits on mount
            this.emitInterface();
        }
    }
</script>

<template>
    <div class="modal" :class="{'is-active': active}">
        <div class="modal-background"></div>

        <div class="modal-content">
            <header class="modal-card-head">
                <p class="modal-card-title">Zadziaa ci si krzywda?</p>
                <button class="delete" @click="active = false"></button>
            </header>
            <section class="modal-card-body" v-if="state == 'input'">
                <p class="title is-4 is-spaced has-text-black">Ile obrae dostae?</p>
                <div class="control mb-5">
                    <div class="field has-addons">
                        <div class="control">
                            <input class="input is-large" v-model="damage" type="number">
                        </div>

                        <div class="control">
                            <button class="button is-large" type="button" @click="damage--">
                                <span class="icon is-small">
                                    <i class="fa-solid fa-minus"></i>
                                </span>
                            </button>
                        </div>

                        <div class="control">
                            <button class="button is-large" type="button" @click="damage++">
                                <span class="icon is-small">
                                    <i class="fa-solid fa-plus"></i>
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
                <p class="title is-4 is-spaced has-text-black">Jak nosisz zbroj?</p>
                <p class="subtitle is-6 has-text-black">
                    Zsumuj twj pancerz i wszystkie modyfikatory, masz na sobie zaklcie Protect?
                </p>
                <div class="control mb-3">
                    <div class="field has-addons">
                        <div class="control">
                            <input class="input is-large" v-model="armor" type="number">
                        </div>

                        <div class="control">
                            <button class="button is-large" type="button" @click="armor--">
                                <span class="icon is-small">
                                    <i class="fa-solid fa-minus"></i>
                                </span>
                            </button>
                        </div>

                        <div class="control">
                            <button class="button is-large" type="button" @click="armor++">
                                <span class="icon is-small">
                                    <i class="fa-solid fa-plus"></i>
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
                <p class="subtitle is-6 has-text-black">
                    W <router-link class="link" to="/rulebook">zasadach gry</router-link> masz info o zbrojach i obraeniach
                </p>
            </section>
            <section class="modal-card-body" v-if="state == 'result'">
                <div class="media">
                    <div class="media-left">
                        <p class="title is-large has-text-black">
                            {{result}}!
                        </p>
                    </div>
                    <div class="media-content">
                        <p class="title is-4 has-text-black">Zostao ci {{currentHP}} HP. {{resultText}}</p>
                        <p class="subtitle is-6 has-text-black">{{resultSubText}}</p>
                    </div>
                </div>
            </section>
            <footer class="modal-card-foot" v-if="state == 'input'">
                <button class="button is-danger" @click="harm()">
                    <span>Przyjmij na klat!</span>
                    <span class="icon is-small">
                        <i class="fa-solid fa-heart-crack"></i>
                    </span>
                </button>
                <button class="button" @click="active = false">Anuluj</button>
            </footer>
            <footer class="modal-card-foot" v-if="state == 'result'">
                <button class="button is-success" @click="active = false">OK</button>
                <button class="button is-danger" @click="show()">Jeszcze mi mao!</button>
            </footer>
        </div>
    </div>
</template>